<template>
    <div class="head-container">
      <div class="header">
        <div class="title">
          <span class="text-wrapper">
            <span v-if="showWelcome" :class="['welcome-text', { 'fade-out': isFadingOut }]">
              Welcome! Thanks for stopping by.
            </span>
            <span v-if="showMain" class="main-text fade-in">
              I'm <span class="title-small">Izaac Plambeck</span> A.K.A. <span class="highlighted-name">Beck</span>
            </span>
          </span>
        </div>
      </div>
      <a class="arrow" href="#about">
        <img src="../../assets/caret-down.svg" alt="Scroll down arrow" />
      </a>
    </div>
  </template>
  
  <script lang="ts">
  import { defineComponent, ref, onMounted } from 'vue'
  import './styles.css'
  
  export default defineComponent({
    name: 'HeadComponent',
    setup() {
      const showWelcome = ref(true)
      const isFadingOut = ref(false)
      const showMain = ref(false)
  
      onMounted(() => {
        setTimeout(() => {
          isFadingOut.value = true
          setTimeout(() => {
            showMain.value = true
            showWelcome.value = false
          }, 1500)
        }, 2000)
      })
  
      return {
        showWelcome,
        isFadingOut,
        showMain
      }
    }
  })
  </script>
  
  <style scoped></style>